/* Skills title animation */
.skillsTitle {
  opacity: 0;
}

.skillsTitle.appear {
  animation: skillsTitleAppear 1.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}

@keyframes skillsTitleAppear {
  0% {
    letter-spacing: 1em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

.skillImages {
  max-height: 5vh;
  max-width: 100%;
}

.skillsCard {
  opacity: 0;
}

.skillsCard.appear {
  animation: skillsCardAppear 1s cubic-bezier(0.55, 0.085, 0.68, 0.53) both;
}

@keyframes skillsCardAppear {
  0% {
    filter: blur(12px);
    opacity: 0;
  }
  100% {
    filter: blur(0px);
    opacity: 1;
  }
}

/* When screen is 400px shorten width to 35vh */
@media only screen and (max-width: 500px) {
  .skillsCard.appear {
    width: 35vh;
  }
}

@media only screen and (min-width: 750px) {
  .skillsCard.appear {
    width: 70vh;
  }
}

.skillsVideoWrapper {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
}

.skillsVideoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
